div.animation {
  width: 100%;
  height: 100vh;
  background-color: #e08958;
  position: fixed;
  top: 0;
  z-index: 999;

  display: flex;
  justify-content: center;
  align-items: center;
}

div.animationEnd {
  transition: all 1s ease;
  opacity: 0;
  pointer-events: none;
}

