.svgGroup {
  /* width: 5em; */
  animation: fill 1s ease-in forwards 2.4s;
}

/* Y narrow space-between */
svg:has(> path#first) {
  margin-right: -0.5em;
  margin-left: -0.5em;
}

/* - moving upward */
svg:has(> path#third),
svg:has(> path#seventeenth) {
  transform: translate(0, -150%);
}

/* g moving downward */
svg:has(> path#eighth) {
  transform: translate(0, 25%);
}
/* p moving downward */
svg:has(> path#twentyNinth) {
  transform: translate(0, 22%);
}

/* v narrow space-between */
svg:has(> path#twentyFifth) {
  margin-right: -0.5em;
  margin-left: -0.5em;
}

/* l expand space-between */
svg:has(> path#fifteenth),
svg:has(> path#sixteenth),
svg:has(> path#twentySeventh) {
  margin-right: 0.1em;
  margin-left: 0.1em;
}

