@media screen and (min-width: 480px) {
  /* v narrow space-between */
  svg:has(> path#twentyFifth) {
    margin: 0;
  }
}

@media screen and (min-width: 1100px) {
  /* v narrow space-between */
  svg:has(> path#twentyFifth) {
    margin: 0;
  }
}

@keyframes fadeInside {
  to {
    stroke-dashoffset: 0px;
  }
}

@keyframes fill {
  from {
    fill: transparent; /* cannot use 'none' because distances between none and white cannot be calculated*/
  }
  to {
    fill: white;
  }
}
